import React, { Component } from 'react';
import logo from './images/logo1.png';
import bear from './images/bear-grylls-png-6.png';
import hall from './images/hall.jpg';
import Home from './Home';
import About from './About';
import Join from './Join';
import Contact from './Contact';
import History from './History';

import './App.css';

class App extends Component {
  constructor() {
    super();
    this.state={
      homeVisible : true,
      aboutVisible : false,
      joinVisible : false,
      contactVisible : false,
      historyVisible : false,
    }
    this.handleHomeClick = this.handleHomeClick.bind(this);
    this.handleAboutClick = this.handleAboutClick.bind(this);
    this.handleJoinClick = this.handleJoinClick.bind(this);
    this.handleContactClick = this.handleContactClick.bind(this);
    this.handleHistoryClick = this.handleHistoryClick.bind(this);
  }

  handleHomeClick(){
    this.setState({
      homeVisible : true,
      aboutVisible : false,
      joinVisible : false,
      contactVisible : false,
      historyVisible : false,
    })
  }
  handleAboutClick(){
    this.setState({
      homeVisible : false,
      aboutVisible : true,
      joinVisible : false,
      contactVisible : false,
      historyVisible : false,
    })
  }
  handleJoinClick(){
    this.setState({
      homeVisible : false,
      aboutVisible : false,
      joinVisible : true,
      contactVisible : false,
      historyVisible : false,
    })
  }
  handleContactClick(){
    this.setState({
      homeVisible : false,
      aboutVisible : false,
      joinVisible : false,
      contactVisible : true,
      historyVisible : false,
    })
  }
  handleHistoryClick(){
    this.setState({
      homeVisible : false,
      aboutVisible : false,
      joinVisible : false,
      contactVisible : false,
      historyVisible : true,
    })
  }
  render() {
    let content;
    if (this.state.homeVisible){
      content = <Home/>;
    }else if(this.state.aboutVisible){
      content = <About/>;
    }else if(this.state.joinVisible){
      content = <Join/>;
    }else if(this.state.contactVisible){
      content = <Contact/>;
    }else if(this.state.historyVisible){
      content = <History/>;
    }
    return (
      <div>
        <img src={logo} alt="group logo" class="center" />
        <div class="navbar" id="topNavbar">
          <button onClick={this.handleHomeClick}>Home</button>
          <button onClick={this.handleAboutClick}>About</button>
          <button onClick={this.handleJoinClick}>Join</button>
         <button onClick={this.handleContactClick}>Contact us</button>
         <button onClick={this.handleHistoryClick}>History</button>
        </div>

        {content}

        <div class="grid-container grid-container--fit">
          <div class="useful-links">
            <h3 >Useful Links</h3>
            <hr style={{color: "#7413dc"}} />
            <p>Home</p>
            <p>About</p>
            <p>Beavers</p>
            <p>Cubs</p>
            <p>Scout</p>
            <p>Explorers</p>
          </div>
          <div class="more-info">
            <h3>More info</h3>
            <hr style={{color:"#006ddf"}} />
          </div>
          <div class="follow-us">
            <h3>Follow us</h3>
            <hr style={{color:"#25b755"}} />
            <p><a href="https://www.facebook.com/ScoutingInJohnstone/" style={{color:"#25b755", margin: 0}}>Facebook</a></p>
          </div>
          <div class="contact-us">
            <h3>Contact us</h3>
            <hr style={{color:"#ed3f23"}} />
          </div>
        </div>

      </div >
    );
  }
}

export default App;
