import React, { Component } from "react";
import bear from './images/bear-grylls-png-6.png';
import hall from './images/hall.jpg';
import './App.css';
class Home extends Component {
  constructor() {
    super();
  }
  render() {
    return (
      <div>
        <div class="container">
          <img src={hall} style={{width: "100%"}} />
          <div class="bottom-left">
            <h1> Scout Hall, Canal Road, Johnstone, PA5 8LA</h1>
          </div>
        </div>

        <div class="grid-container grid-container--fit">
          <div class="grid-element-welcome">
            <h1 style={{ 'font-size': '4vw', 'margin-bottom': 0 }}> Welcome to </h1>
            <h1 style={{ 'font-size': '5vw', color: "#006ddf", 'margin-top': 0, 'margin-bottom': 0 }}> 1st/3rd Johnstone </h1>
            <h1 style={{ 'font-size': '5vw', color: "#006ddf", 'margin-top': 0 }}> Central Scout Group</h1>
          </div>
          <div class="grid-element-welcome">
            <div class="container">
              <img src={bear} alt="bear-grylls-png-6" style={{ width: '100%' }} />
              <div>
                <button onclick="window.location.href='join.ht';" class="button">Join the adventure</button>
              </div>
            </div>
          </div>
        </div>

        <hr />

        <h1> Aged 6 to 18?</h1>
        <div class="grid-container grid-container--fit">
          <div class="grid-element">
            <h1 style={{ 'font-size': '4vw', 'text-align': 'center', color: "#006ddf" }}>Beavers</h1>
            <h2 style={{ 'text-align': 'center', 'font-size': '2vw' }}>Aged 6 to 8</h2>
            <div class="overlay">
              <div class="text"><a href="beaver.html">Find out More</a> </div>
            </div>
          </div>
          <div class="grid-element">
            <h1 style={{ 'font-size': '4vw', 'text-align': 'center', color: "#23a950" }}>Cubs</h1>
            <h2 style={{ 'text-align': 'center', 'font-size': '2vw' }}>Aged 8 to 10½</h2>
            <div class="overlay">
              <div class="text"><a href="cubs.html">Find out More</a></div>
            </div>
          </div>
          <div class="grid-element">
            <h1 style={{ 'font-size': '4vw', 'text-align': 'center', color: "#004851" }}>Scouts</h1>
            <h2 style={{ 'text-align': 'center', 'font-size': '2vw' }}>Aged 10½ to 14</h2>
            <div class="overlay">
              <div class="text"><a href="scouts.html">Find out More</a></div>
            </div>
          </div>
          <div class="grid-element">
            <h1 style={{ 'font-size': '4vw', 'text-align': 'center', color: "#003982" }}>Explorers</h1>
            <h2 style={{ 'text-align': "center", 'font-size': '2vw' }}>Aged 14 to 18</h2>
            <div class="overlay">
              <div class="text"><a href="explorers.html">Find out More</a> </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
export default Home;
